@import './icons/styles'

@mixin phc ($css)
    ::-webkit-input-placeholder $css
    :-moz-placeholder           $css
    ::-moz-placeholder          $css
    :-ms-input-placeholder      $css

*
    padding: 0
    margin: 0
    box-sizing: border-box

$grayDefault: #adafaf
$grayColor: #4a4a4a
$grayLight: #f5f5f5
$redColor: #e30611

#app
    width: 100%
    min-width: 320px
    display: block

.landing-page
    background: #fff
    font-family: MTSSans
    overflow-x: hidden
    text-align: left
    min-width: 320px

.form
    &__line
        margin-bottom: 20px
    &__input
        width: 100%
        height: 50px
        font-size: 18px
        padding-left: 20px
        background: transparent
        border: none
        border-bottom: 1px solid $grayDefault
        color: $grayDefault
        opacity: 1
        outline: none
        color: inherit
        +phc($grayDefault)
        transition: all 0.3s ease 0s
        &:invalid
            box-shadow: none
        &.invalid
            box-shadow: none
            border-bottom: 1px solid $redColor
            color: $redColor
    &__button
        padding-top: 15px

.hamburger
    display: inline-block
    cursor: pointer
    outline: none
    border: none
    position: relative
    width: 35px
    height: 30px
    padding: 0
    &__line
        width: 100%
        height: 4px
        background-color: $redColor
        position: absolute
        top: 50%
        margin-top: -2px
        &_top
            top: 0
            margin-top: 0
        &_bottom
            top: auto
            margin-top: 0
            bottom: 0

.button
    display: inline-block
    outline: none
    border: none
    min-width: 260px
    font-size: 18px
    font-weight: normal
    font-stretch: normal
    font-style: normal
    line-height: 1.67
    letter-spacing: normal
    text-align: center
    color: #fefefe
    background: $redColor
    padding: 17px 64px
    border-radius: 5px
    transition: all 0.3s ease 0s
    cursor: pointer
    position: relative
    &:disabled
        background: $redColor
        &:hover
            background: $redColor
    &:hover
        color: #fefefe
        background: #b1050d
    &:focus
        outline: 0 !important
    &:active:not(:disabled)
        top: 2px
        left: 2px

.text
    &_fz_18
        font-size: 18px
    &_lh_17
        line-height: 1.7
    &_ls_05
        letter-spacing: 0.5px

.list
    counter-reset: listItem 0
    padding: 0
    display: block
    list-style-type: none
    &_text_gray
        color: $grayColor
    &__item
        font-size: 18px
        line-height: 1.7em
        padding: 0
        margin: 0
        padding-left: 45px
        position: relative
        margin-bottom: 40px
        &:last-child
            margin-bottom: 0
        &:before
            counter-increment: listItem
            content: counter(listItem)
            color: red
            position: absolute
            left: 0
            top: 0

.st-text
    font-size: 16px
    font-weight: normal
    font-stretch: normal
    font-style: normal
    line-height: 1.5
    letter-spacing: normal
    color: rgba(255, 255, 255, 0.45)


.container
    max-width: 1330px
    padding: 0 15px
    margin: 0 auto

.main-header
    &__logo
        font-size: 19px
        transition: all 0.3s ease 0s
        color: inherit
        &:hover
            color: $redColor
    &__container
        display: flex
        align-items: center
        justify-content: space-between

.landing-page
    position: relative
    &_message
        .landing-page__message
            opacity: 1
        .landing-page__overlay
            opacity: 0.5
    &__message
        text-align: center
        width: 300px
        background: #fff
        position: fixed
        left: 50%
        margin-left: -100px
        top: 50%
        transform: translateY(-50%)
        font-size: 30px
        line-height: 1.5em
        padding: 15px
        border-radius: 10px
        opacity: 0
        transition: all 0.3s ease 0s
        z-index: 200
        pointer-events: none
    &__overlay
        background: #000
        position: fixed
        left: 0
        top: 0
        right: 0
        bottom: 0
        opacity: 0
        z-index: 140
        pointer-events: none
        transition: all 0.5s ease 0s
    &__header
        color: #fff
        position: absolute
        left: 0
        top: 40px
        width: 100%
        z-index: 50
    &__footer
        padding: 70px 0
    &__hamburger
        display: none
        font-size: 40px
        cursor: pointer


.landing-page-menu
    text-align: right
    &__link
        padding: 10px
        display: inline-block
        transition: all 0.3s ease 0s
        color: inherit
        text-decoration: none
        &:hover
            color: $redColor
        &:not(:last-child)
            margin-right: 70px

.main-screen
    height: 100vh
    min-height: 800px
    color: #fff
    background-image: url('%=static=%content/bg-img.png')
    background-position: top center
    background-color: #fff
    background-size: cover
    background-repeat: no-repeat
    &__container
        height: 100%
        position: relative
        padding-top: 180px
        z-index: 20
        &:before
            content: ''
            width: 66px
            height: 758px
            top: 0
            left: 15px
            position: absolute
            background-image: url("%=static=%content/logo-line.png")
            background-size: contain
            background-position: center
    &__content
        text-align: left
        position: relative
        z-index: 20
        padding-left: 115px
    &__title
        font-size: 100px
        font-weight: 900
        font-stretch: normal
        font-style: normal
        line-height: 1.2em
        letter-spacing: normal
        text-transform: uppercase
        color: #fefefe
        margin-bottom: 45px
        margin-left: -90px
    &__descr
        font-size: 18px
        font-weight: normal
        font-stretch: normal
        font-style: normal
        line-height: 1.67
        letter-spacing: normal
        color: #fefefe
        margin-bottom: 55px

.full-screen
    height: 100vh
    min-height: 800px
    color: #fff
    background-color: #fff
    background-size: cover
    background-repeat: no-repeat
    &_bg1
        background-image: url('%=static=%/content/employeesMainBg.png')
        background-position: center top
    &_bg2
        background-image: url('%=static=%/content/chiefsMainBg.png')
        background-position: center top
    &__content
        padding-top: 260px
    &__title
        line-height: 1.12 !important
        margin-bottom: 35px


.screen-box-image
    height: 510px
    background: $grayLight
    border-radius: 0 40px 40px 0
    position: relative
    &_h_400
        height: 400px
    &_h_634
        height: 634px
    &:before
        content: ''
        position: absolute
        right: 100%
        top: 0
        bottom: 0
        width: 10000px
        background: $grayLight
    &:after
        content: ''
        position: absolute
        background-size: contain
        background-repeat: no-repeat
        background-position: center
    &_right
        border-radius: 10% 0 0 10%
        &:before
            right: auto
            left: 100%
    &_bg1
        &:after
            bottom: 0
            right: -90px
            width: 774px
            height: 629px
            background-image: url('%=static=%/content/welcome-screen__employees__background.png')
    &_bg2
        &:after
            bottom: 0
            left: -150px
            width: 921px
            height: 623px
            background-image: url("%=static=%/content/welcome-screen__chief__background.png")
    &_bg3
        &:after
            top: 0
            right: -55px
            width: 497px
            height: 587px
            background-image: url(%=static=%/content/employees-screen__schedule.png)
    &_bg4
        &:after
            top: 0
            right: -30px
            width: 469px
            height: 589px
            background-image: url(%=static=%/content/employees-screen__handbook.png)
    &_bg5
        &:after
            top: 0
            right: -150px
            width: 845px
            height: 634px
            background-image: url(%=static=%/content/chiefs-screen__schedule.png)
    &_bg6
        &:after
            top: 0
            right: -160px
            width: 788px
            height: 634px
            background-image: url(%=static=%/content/chiefs-screen__handbook.png)

.section-grid
    display: flex
    align-items: center
    justify-content: space-between
    &_top
        align-items: flex-start
    &__item
        &.w-20
            width: 20%
        &.w-25
            width: 25%
        &.w-42
            width: 42%
        &.w-48
            width: 48%
        &.w-62
            width: 61%

.testing-section
    padding: 100px 0
    color: #fff
    background-image: url("%=static=%/content/welcome-screen__testing__background.png")
    background-size: cover
    background-position: center
    background-repeat: no-repeat
    &__container
        max-width: 640px
    &__item
        margin-bottom: 60px
        &:last-child
            margin-bottom: 0

.section
