            position: absolute
            right: 0
            bottom: 0
            width: 100%
            height: 100%
            background-image: url(%=static=%/content/employees-screen__kpi.png)
            background-size: auto
            background-repeat: no-repeat
            background-position: right center
    &_communication
        padding: 220px 0
        &:after
            content: ''
            position: absolute
            right: 0
            bottom: 0
            width: 100%
            height: 100%
            background-image: url(%=static=%/content/EmployeesScreenCommunication.png)
            background-size: auto
            background-repeat: no-repeat
            background-position: right center
    &_features
        padding: 140px 0
        &:after
            content: ''
            position: absolute
            right: 0
