            width: 672px
            height: 696px
            background-image: url('%=static=%/content/welcome-screen__features__options.png')
            background-size: contain
            background-repeat: no-repeat
            background-position: center
    &_employees
        padding: 200px 0 50px
    &_chief
        padding: 50px 0 100px
    &_roles
        padding: 140px 0
    &_p_140
        padding: 140px 0
    &_bg_gray
        background-color: $grayLight
    &__container
        position: relative
        z-index: 20
    &__content
        width: 76%
        &_mw_440
            max-width: 440px
        &_mw_520
            max-width: 520px

.welcome-logo
    font-size: 20px
    color: $redColor

.landing-footer
    display: flex
    justify-content: space-between
    align-items: center
    &_main
        justify-content: flex-start
        .landing-footer__menu
            padding-left: 220px

.footer-menu
    font-size: 14px
    color: $grayDefault
    &_main
        color: #fff
        .footer-menu__link:first-child
            color: $grayColor
            &:hover
                color: $redColor
    &__link
        padding: 5px
        display: inline-block
        transition: all 0.3s ease 0s
        color: inherit
        text-decoration: none
        &:hover
            color: $redColor
        &:not(:last-child)
            margin-right: 90px

.callback-section
    &__container
        padding: 115px 15px 200px
        position: relative
        &:after
            content: ''
            position: absolute
            bottom: 0
            right: 0
            width: 850px
            height: 650px
            background-image: url("%=static=%/content/welcome-screen__callback__background.png")
            background-position: bottom right
            background-repeat: no-repeat
            background-size: 100% auto
    &__main
        max-width: 415px
        position: relative
        z-index: 20
    &__title
        margin-bottom: 25px
    &__descr
        color: $grayDefault
        font-size: 18px
        line-height: 1.33
        margin-bottom: 50px
    &__form
        padding-right: 15px
    &__footer
        position: absolute
        z-index: 30
        left: 15px
        right: 15px
        bottom: 70px



.title
    font-weight: normal
    font-stretch: normal
    font-style: normal
    line-height: 1.4
    letter-spacing: normal
    &_bold
        font-weight: bold
    &_fz_18
        font-size: 18px
    &_fz_22
        font-size: 22px
    &_fz_30
        font-size: 30px
    &_fz_50
        font-size: 50px
    &_fw_500
        font-weight: 500
    &_mb_50
        margin-bottom: 50px

.descr-box
    position: relative
    padding-top: 35px
    font-size: 18px
    line-height: 1.67em
    color: $grayColor
    &:before
        content: ''
        background: $redColor
        position: absolute
        left: 0
        top: 0px
        width: 45px
        height: 4px


.grid
    display: flex
    flex-wrap: wrap
    margin: -60px -35px 0
    &_2el
        .grid__item
            width: 50%
    &__item
        width: 33.33%
        padding: 60px 35px 0


.infographic-box
    &__icon
        line-height: 45px
        font-size: 45px
        display: inline-block
        color: transparent
        background-image: linear-gradient(90deg, rgba(119,4,7,1) 0%, rgba(237,61,66,1) 45%, rgba(242,94,104,1) 91%, rgba(244,97,108,1) 97%)
        background-clip: text
        margin-bottom: 30px
    &__title
        margin-bottom: 15px
    &__descr
        font-size: 16px
        font-weight: normal
        font-stretch: normal
        font-style: normal
        line-height: 1.5
        letter-spacing: normal
        color: rgba(0, 0, 0, 0.45)


.roles-box
    display: flex
    align-items: center
    justify-content: space-between
    &__title
        margin-bottom: 40px
    &__descr
        color: #adafaf
        font-size: 18px
    &__content
        width: 30%
    &__grid
        width: 68%
        display: flex
        justify-content: center
    &__item
        width: 360px
        margin: 0 15px

.roles-box-popup
    &__wrapper
        max-width: 330px
    &__title
        margin-bottom: 40px


.roles-box-item
    position: relative
    cursor: pointer
    &:hover
        .roles-box-item__popup
            opacity: 1
    &__popup
        opacity: 0
        transition: all 0.3s ease 0s
        position: absolute
        border-radius: 22px
        box-shadow: 0px 8px 40px 0 rgba(0, 0, 0, 0.05)
        width: 500px
        height: 500px
        left: -142%
        top: 50%
        transform: translateY(-56%)
        z-index: 20
        background: #fff
        display: flex
        align-items: center
        justify-content: center
        pointer-events: none
        &_right
            left: auto
            right: -142%
    &__name
        font-size: 18px
        line-height: 1.33
        letter-spacing: normal
        text-align: center
        color: #adafaf
    &__image
        background-color: $grayLight
        border-radius: 22px
        position: relative
        padding-bottom: 100%
        width: 100%
        margin-bottom: 30px
        &:before
            content: ''
            position: absolute
            right: 0
            bottom: 0
        &_bg1:before
            width: 112%
            height: 117%
            background-image: url("%=static=%/content/chief.png")
            background-size: contain
            background-repeat: no-repeat
            background-position: right bottom
        &_bg2:before
            right: -20px
            width: 100%
            height: 116%
            background-image: url("%=static=%/content/employee.png")
            background-size: contain
            background-repeat: no-repeat
            background-position: center

.ie11
    .infographic-box
        &__icon
            color: $redColor
            background-image: none
            background-clip: unset

@media screen and (max-width: 1750px)
