        &_roles
            padding: 100px 0

    .roles-box
        display: block
        &__title
            margin-bottom: 20px
        &__content
            margin-bottom: 80px
            width: 100%
            text-align: center
            br
                display: none
        &__grid
            width: 100%


    .roles-box-item
        max-width: 400px
        margin: 0 auto
        cursor: default
        &__popup
            position: static
            opacity: 1
            width: 100%
            height: auto
            transform: none
            box-shadow: none
