        min-height: 100%
        background-position: 70% center
        &__container
            padding: 80px 15px
            br
                display: none
            &:before
                display: none
        &__title
            margin-left: 0
            font-size: 40px
            margin-bottom: 20px
        &__descr
            font-size: 16px
        &__content
            padding-left: 0

    .testing-section
        padding: 30px 0
        background-position: 70% center
        &__item
            margin-bottom: 20px

    .title
        &_fz_22
            font-size: 20px
        &_fz_30
            font-size: 24px

    .section
