            width: 50%
            padding: 30px 15px 0

    .descr-section
        padding: 30px 0
        &__container
            display: block
        &__content
            width: 100%
        &__image
            display: none

    .section-grid
        &__item
            max-width: 500px


@media screen and (max-width: 550px)
