        &__menu
            width: 200px
    .roles-box
        &__grid
            display: block
        &__item
            width: 100%
            margin: 0
            &:not(:last-child)
                margin-bottom: 70px

    .infographic-box
        &__icon
            margin-bottom: 15px
        &__title
            margin-bottom: 15px

    .grid
        display: block
        margin: 0 auto
        max-width: 350px
        text-align: center
        &__item
            width: 100% !important
            padding: 0
            margin-bottom: 20px
            &:last-child
                margin-bottom: 0

    .testing-section
        br
            display: none
    .section
        &_features
            .title
                text-align: center
        &_bg1:after
            display: none